                    }
                });
                console.log("Object.keys(cartonDetails).length 2",Object.keys(this.cartonDetails).length);
            } else if (packageName == "Reel") {
                axios.get("/Packages/TraceReel", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                    params: {reelCode:this.codeValue},
                }).then((response) => {
                    this.showCoilBox = true;
                    this.showCartonBox = false;
                    this.showPalletBox = false;
                    let reelDet = response.data;
                    this.reel.reelCode = reelDet.code;
                    this.reel.reelSize = reelDet.reelSize;
                    this.reel.productStatus = reelDet.status;
                    this.reel.printOrderNumber = reelDet.printOrderNumber;
                    this.reel.productType = reelDet.productNameOrgin;
                    this.reel.cartonCode = reelDet.cartonCode;
                    if(reelDet.traceOrderDetailDto) {
                        this.manufacturer.manufacturerName = reelDet.traceOrderDetailDto.organizationName;
                        this.manufacturer.orderNumber = reelDet.traceOrderDetailDto.orderNumber;
                        this.manufacturer.productName = reelDet.traceOrderDetailDto.brandProductName;
                        this.manufacturer.productSKU = reelDet.traceOrderDetailDto.skuName;
                        this.manufacturer.warehouseLocation = reelDet.traceOrderDetailDto.warehouseName;
                        if(reelDet.traceOrderDetailDto.stockRequestNumber !== null || reelDet.traceOrderDetailDto.stockEarlierFacility !== null || reelDet.traceOrderDetailDto.stockCurrentFacility !== null) {
                            this.stockReq.orderNumber = reelDet.traceOrderDetailDto.stockRequestNumber;
                            this.stockReq.currentFacility = reelDet.traceOrderDetailDto.stockCurrentFacility;
                            this.stockReq.earlierFacility = reelDet.traceOrderDetailDto.stockEarlierFacility;
                        } else {
                            this.stockReq = {};
                        }
                    } else {
                        this.manufacturer = {};
                    }
                    if(reelDet.returnOrderDto) {
                        console.log("if reelDet",reelDet);
                        this.retOrder.orderNumber = reelDet.returnOrderDto.number;
                        this.retOrder.originalManufacturer = reelDet.returnOrderDto.organization;
                        this.retOrder.returnDate = reelDet.returnOrderDto.expectedDeliveryDate;
                    } else {
                        console.log("else reelDet",reelDet);
                        this.retOrder = {};
                    }
                }).catch(err => {
                    if (err.response) {
                        // alert(err.response.data);
                        toast.warning(err.response.data);
                    } else if (err.request) {
                        console.log(err.request);
                    } else {
                        console.log(err);
                    }
                });
            } else if (packageName == "Pallet") {
                axios.get("/Packages/TracePallet", {
                    baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl,
                    params: {palletCode:this.codeValue},
                }).then((response) => {
                    this.showCoilBox = false;
                    this.showCartonBox = false;
                    this.showPalletBox = true;
                    console.log("Pallet",response.data);
                    this.palletDetails.palletCode = response.data.code;
                    this.palletDetails.cartonListing = response.data.cartons;
                }).catch(err => {
                    if (err.response) {
                        // alert(err.response.data);
                        toast.warning(err.response.data);
                    } else if (err.request) {
                        console.log(err.request);
                    } else {
                        console.log(err);
                    }
                });
            }
        },
        setDetails(code,type) {
            this.selectedPackage.text = type;
            this.selectedPackage.id = this.packageObj[type];
            this.codeValue = code;
            this.fetchDetails();
        },
        onPackageChange() {
            this.codeValue = "";
            this.showCoilBox = false;
            this.showCartonBox = false;
            this.showPalletBox = false;
        },
        getPackageTypes() {
            axios.get("/Packages/GetPackageTypes", {
                baseURL: process.env.VUE_APP_OrderMgmtApisBaseUrl
            }).then((response) => {
                var packageTypes = [], packageObj = {};
                for (let key in response.data) {
                    packageTypes.push({'productId': key, 'productName': response.data[key]});
                    packageObj[response.data[key]] = key;
                }
                this.packageList = packageTypes;
                this.packageObj = packageObj;
            });
        },
    }
};
</script>